const store = {
    state: {
        hotSearchOffsetY: 0,
        flapCardVisible: false,
        isEditMode: false, // 
        shelfList: [], // 
        shelfSelected: [], // 
        shelfTitleVisible: true, // 
        shelfFooterShowVisible: false, // 
        shelfFooterPopupProps: {}, // shelf-list
        // 
        shelfCategory: null,
        // 12
        currentType: 1,
        dialogProps: { showNewGroup: false, groupName: '' } // dialogPropsprops
    },
    mutations: {
        SET_HOT_SEARCH_OFFSETY(state, offsetY) {
            state.hotSearchOffsetY = offsetY
        },
        SET_FLAP_CARD_VISIBLE(state, visible) {
            state.flapCardVisible = visible
        },
        SET_IS_EDIT_MODE: (state, isEditMode) => {
            state.isEditMode = isEditMode
        },
        SET_SHELF_SELECTED: (state, shelfSelected) => {
            state.shelfSelected = shelfSelected
        },
        SET_SHELF_TITLE_VISIBLE: (state, shelfTitleVisible) => {
            state.shelfTitleVisible = shelfTitleVisible
        },
        SET_SHELF_LIST: (state, shelfList) => {
            state.shelfList = shelfList
        },
        setShelfFooterShowVisible: (state, shelfFooterShowVisible) => {
            state.shelfFooterShowVisible = shelfFooterShowVisible
        },
        setShelfFooterPopupProps: (state, shelfFooterPopupProps) => {
            state.shelfFooterPopupProps = shelfFooterPopupProps
        },
        set_shelfCategory: (state, shelfCategory) => {
            state.shelfCategory = shelfCategory
        },
        set_currentType: (state, currentType) => {
            state.currentType = currentType
        },
        set_dialogProps: (state, dialogProps) => {
            state.dialogProps = dialogProps
        }
    }
}

export default store
